*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: hsl(252, 100%, 77%);
  color: white;
}

/* main section */
.App {
  margin: 3rem auto 0 auto;
  display: grid;
  justify-content: center;
  align-content: center;
  min-height: 500px;
  max-width: 500px;
  border: 4px solid white;
  border-radius: 1rem;
  grid-template-rows: 1 fr 0.1fr 0.1fr;
  gap: 3rem;
  padding-block: 5rem;
}

/* todo area  */
.search_todos {
  display: grid;
  align-content: start;
  gap: 1.5rem;
  min-height: 150px;
}

/* searchbar  */
.searchbox {
  min-height: 2.5rem;
  display: grid;
  grid-auto-flow: column;
  border: 1px solid white;
  border-radius: 0.6rem 0 0 0.6rem;
}

.searchbox_input {
  border-radius: 0.5rem 0 0 0.5rem;
  border: none;
  min-width: 16rem;
  padding-left: 1rem;
  outline: none;
  padding-left: 1rem;
}

.searchbox_placeholder {
  align-self: center;
  margin-inline: 0.8rem;
  cursor: pointer;
}

/* todos area */
.todos-area {
  display: grid;
  gap: 0.7rem;
}

.task {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.checkTask {
  margin-right: 0.5rem;
  cursor: pointer;
}

.taskName {
  cursor: pointer;
  font-size: 1.1rem;
}

.edit_input {
  border: none;
  outline: none;
  min-height: 1.2rem;
  border-radius: 0.2rem;
  padding-left: 0.7rem;
}

.modify_icon {
  margin-left: 0.5rem;
  font-size: 1.2rem;
  cursor: pointer;
}

.modify_icon:is(:hover, :focus) {
  color: black;
  transition: color 500ms ease-in;
}

.doneStyle {
  text-decoration: line-through;
  opacity: 0.5;
}

.seleted-task button {
  margin-right: 1rem;
  border: 1px solid white;
  outline: none;
  min-width: 60px;
  min-height: 1.3rem;
  border-radius: 0.3rem;
  font-weight: bold;
  color: hsl(252, 100%, 77%);
  cursor: pointer;
}

.seleted-task button:is(:hover, :focus) {
  background-color: hsl(252, 100%, 77%);
  color: white;
  transition: color 300ms ease-in, background-color 300ms ease-in;
}

.addTodo_btn {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  border: 1px solid white;
  padding: 0.3rem;
  width: 50%;
  cursor: pointer;
}

.addTodo_btn:is(:hover, :focus) {
  border: 1px solid hsl(252, 100%, 77%);
  background-color: white;
  color: hsl(252, 100%, 77%);
  transition: color 300ms ease-in-out, background 300ms ease-in-out;
}

.addTodo_input {
  display: flex;
  align-items: center;
  border: 1px solid white;
  width: min-content;
  border-radius: 0.4rem;
}

.addTodo_input input {
  min-height: 1.8rem;
  min-width: 16rem;
  outline: none;
  border: none;
  border-radius: 0.3rem 0 0 0.3rem;
  padding-left: 1rem;
}

.addTodo_input_btn {
  cursor: pointer;
  background: none;
  padding: 0px;
  border: none;
}

.addTodo_input_btn-icon {
  background-color: hsl(252, 100%, 77%);
  min-height: 1.1rem;
  min-width: 2.3rem;
  color: white;
}

.addTodo_input_btn-icon:hover {
  color: black;
  transition: color 300ms ease-in-out;
}

@media (max-width: 400px) {
  .App {
    border: none;
  }
}
